
.main-menu-container {
  display: flex;
  flex-direction: column;
  background-color: #eff6ff; /* bg-blue-50 */
}

.main-menu-title-bar {
  background-color: #2563eb; /* bg-blue-700 */
  color: white;
  font-size: 1.5rem; /* text-2xl */
  line-height: 2rem;
  font-weight: 700; /* font-bold */
  text-align: center;
  padding-top: 1rem; /* py-4 */
  padding-bottom: 1rem;
  flex-shrink: 0; /* ÌÉÄÏù¥ÌãÄ Î∞îÎäî Ï§ÑÏñ¥Îì§ÏßÄ ÏïäÎèÑÎ°ù */
}

.main-menu-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(2, minmax(0, 1fr)); /* Í∞Å ÌñâÏù¥ ÏÇ¨Ïö© Í∞ÄÎä•Ìïú Í≥µÍ∞ÑÏùÑ Í∑†Îì±ÌïòÍ≤å ÎÇòÎàÑÎèÑÎ°ù Ïú†ÏßÄ */
  flex-grow: 1;
